<template>
  <div class="content-view">
    <div class="content-list">
      <div class="content-list-group" v-for="(group, index) in postData.postList" :key="index">
        <div class="content-list-group-title">
          {{ group.groupName }}
        </div>
        <router-link class="content-list-item" v-for="(item, index) in group.subs" :key="item" :to="{ name: detail, params: { componentName: item } }" :title="postData.postMap[item].postTitle">
          <i class="content-list-item-icon" :class="postData.postMap[item].iconClass"></i>
          <span>{{ postData.postMap[item].postTitle }}</span>
        </router-link> 
      </div>
    </div>
    <div class="content-body">
      <router-view class="content-body-inner" :post-url="postUrl" :post-timestamp="postTimestamp"></router-view>
    </div>
  </div>
</template>

<script>
import postData from './assets/postList'

export default {
  name: 'componentView',
  data () {
    return {
      detail: this.$route.meta.detail,
      postData: postData
    }
  },
  created () {
    if (!this.$route.params.componentName) {
      this.$router.push({
        name: this.detail,
        params: {
          componentName: postData.postList[0].subs[0]
        }
      })
    }
  },
  computed: {
    postUrl () {
      let _url = ''
      try {
        _url = this.postData.postMap[this.$route.params.componentName] ? this.postData.postMap[this.$route.params.componentName].postUrl : ''
      } catch (e) {}
      return _url
    },
    postTimestamp () {
      let _timestamp = ''
      try {
        _timestamp = this.postData.postMap[this.$route.params.componentName] ? this.postData.postMap[this.$route.params.componentName].timestamp : ''
      } catch (e) {}
      return _timestamp
    }
  },
  methods: {
  },
  components: {
  }
}
</script>

<style src="./component.less" lang="less"></style>



